.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-footer {
  background-color: #222;
  font-size: large;
  height: 80px;
  padding: 20px;
  color: white;
}
.container {
  background-color: #f2f2f2;
  border-radius: 7px;
}

.languageSelection p {
  text-align: right;
  margin-right: 50px;
  padding-top: 5px;
}

.languageSelection p a {
  padding-left: 10px;
  padding-right: 10px;
  font-size: large;

}

.languageSelection p a:hover {
  background-color: lightgray;
  border-radius: 10;
  text-decoration: underline;
}

.home-style {
  background-color: '#222';
  padding: 20;
  color: 'white';
  border-radius: 4;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



